import { useEffect } from "react";
import { useLocation } from "react-router-dom";



/**
 * @description  ,   
 * @returns null
 */

export default function ScrollToTop() {
  const { pathname } = useLocation();
  
  useEffect(() => {
    // window.scrollTo(0, 0);
    document.querySelector('main').scrollIntoView({
      block: 'start',
      behavior: 'auto'
    });
  }, [pathname]);
  
  return null;
}
